import React,{useState,useEffect} from 'react'
// import Contacts from './Contacts'

function Contactform({addorEdit,currid,contactsobj}) {
    const initialfieldvalue={
        fullname:'',
        Mobile:'',
        email:'',
        address:'',
    }
    const [user,setUser]=useState(initialfieldvalue)
    console.log("heyyyyyy",contactsobj)
    console.log("heyyyyyy",currid)

    useEffect(()=>{
        if(currid==''){
            setUser({
                ...initialfieldvalue
            })
        
        }
        else{
            setUser({
                ...contactsobj[currid]
            })

        }

    },[currid,contactsobj,]
    )
    
    const handleInputChange=(e)=>{
        var {name,value}=e.target 
        console.log("frommmmmm",e.target)
        console.log(name)
        console.log(value)
        setUser({
            ...user,
            [name]: value
    })
    }
    const handleSubmit=(e)=>{
        e.preventDefault()
    console.log(user)
    // console.log("this is propppppp",props.user)

        addorEdit(user)

    }
    return (
        <div>
            <form action="" autoComplete="off" onSubmit={handleSubmit}>
                <div className="form-group input-group">
                    <div className="input-group-prepend">
                        <div className="input-group-text">
                            <i className="fas fa-user"></i>
                        </div>
                    </div>
                    <input type="text" className="form-control" placeholder="fullname" name="fullname" value={user.fullname}
                    onChange={handleInputChange} />
                </div>
                <div className="form-row">
                <div className="form-group input-group col-md-6">
                    <div className="input-group-prepend">
                        <div className="input-group-text">
                            <i className="fas fa-mobile-alt"></i>
                        </div>
                    </div>
                    <input type="text" className="form-control" placeholder="Mobile" name="Mobile" value={user.Mobile}
                    onChange={handleInputChange} />
                </div>
                <div className="form-group input-group col-md-6">
                    <div className="input-group-prepend">
                        <div className="input-group-text">
                            <i className="fas fa-envelope"></i>
                        </div>
                    </div>
                    <input type="email" className="form-control" placeholder="email" name="email" value={user.email} 
                    onChange={handleInputChange}/>
                </div>
               
                </div>
                <div className="form-group">
                    <textarea name="address" placeholder="Address" name="address" value={user.address}
                    onChange={handleInputChange}></textarea>

                </div>
                <div className="form-control">
                    <input type="submit" value={currid==''?"Save":"Update"} className="btn btn-primary btn-block" />
                </div>
            </form>
        </div>
    )
}

export default Contactform

