import React,{useState,useEffect} from 'react'
import Container from '@material-ui/core/Container'
import { Typography } from '@material-ui/core'
import Contactform from './Contactform'
import app from '../App'
import firebaseDb from '../firebaseop'
function Contacts() {
    var [contactsobj,setContactsobj]=useState(0)
    var [currid,setCurrid]=useState('')
    useEffect(()=>{
        firebaseDb.child('contacts').on('value',snapshot=>{
            if(snapshot.val()!=null)
                setContactsobj({
                    ...snapshot.val()
                })
            else
                setContactsobj([])
                
        })


    },[])
    const addorEdit=(obj)=>{
        if(currid=='')
            firebaseDb.child('contacts').push(
                obj,
                err=>{
                    if (err)
                        console.log(err)
                    else
                        setCurrid('')
                }
            )
        else
            firebaseDb.child(`contacts/${currid}`).set(
                obj,
                err=>{
                    if (err)
                        console.log(err)
                    else
                        setCurrid('')
                }
            )
            

    }
    const onDelete=key=>{
        if(window.confirm('Are You sure To delete this record')){
            firebaseDb.child(`contacts/${key}`).remove(
                err=>{
                    if(err)
                        console.log(err)
                    else 
                        setCurrid('')
                }
            )
        }

    }
    return (
        <div>
            <div className="jumbotron jumbotron-fluid">
                <div className="container">
                    <h1 className="display-4">Contact Form</h1>
                </div>
            </div>
            <div className="row">
                    <div className="col-md-5">
                        <Contactform  {...({addorEdit,currid,contactsobj})}/>
                    </div>
                    <div className="col-md-7">
                        <table className="table table-borderless table-striped">
                            <thead className="thead-light">
                                <tr>
                                    <th>Full Name</th>
                                    <th>Mobile</th>
                                    <th>Email</th>
                                    <th>Action</th>
                                </tr>
                            </thead>
                            <tbody>
                                {
                                    Object.keys(contactsobj).map(id=>{
                                        return <tr key={id}>
                                            <td>{contactsobj[id].fullname}</td>
                                            <td>{contactsobj[id].email}</td>
                                            <td>{contactsobj[id].Mobile}</td>
                                            <td>{contactsobj[id].address}</td>

                                             <a className="btn text-primary"
                                             onClick={()=>{setCurrid(id)}}>
                                                 <i className="fas fa-pencil-all"></i>
                                                 p
                                             </a>
                                             <a className="btn text-danger" onClick={()=>onDelete(id)}>
                                                 <i className="far fa-trash-all"></i>
                                                 d
                                             </a>
                                        </tr>
                                    })
                                }
                            </tbody>
                        </table>
                    </div>
            </div>
        </div>
        
        
        
    )
}
export default Contacts

