import React,{useState} from 'react'
import { Button } from '@material-ui/core'
// import { WithStyles } from '@material-ui/core'
import { Typography } from '@material-ui/core'
import { typography } from '@material-ui/system'
import { makeStyles } from '@material-ui/core';

const useStyle=makeStyles({
  hellostyle:{
    fontStyle:"oblique",
    color:"red",
    fontSize:"20px"


  },
  buttonStyle:{
      color:"white"
  }
})
function Materialui() {
    const classes=useStyle()

    return (
        <div>
          <Typography className={classes.hellostyle} color="secondary" variant="h1">Hello this is learning</Typography>
          <Button className={classes.buttonStyle} disabled={false}  variant="contained" color="primary" >helllo</Button>
        </div>
    )
}

export default Materialui
